.form {
    position: fixed;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-template-rows: repeat(3,1fr);
    height : 100vh;
    justify-content: right;
    margin-left : 72.5vw;
    button {
        width : 25.6vw;
        font-size : 3rem;
        border : solid 0.5vh #3cbe22;
        outline : none;
        background-color: #1c640e;
    }
    @media screen and (orientation : portrait){
        button {
            font-size : 1.6rem;
            width : 27vw;
        }
    }
    input {
        width : 25vw;
        border : solid 0.5vh #3cbe22;
        color : #3cbe22;
        background-color : transparent;
        font-size : 1.2rem;
    }
    background-color: #222b23;
}
.memes {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width : 70vw;
    background-color: #222b23;
    margin-left : 2.5vw;
}

body { 
    background-color: #222b23;
}
  